import React from "react";
import { graphql, useStaticQuery } from "gatsby";
import { GatsbyImage } from "gatsby-plugin-image"

const CommitteeMemberList = () => {
  const data = useStaticQuery(graphql`{
    allStrapiCommitteeMemberLists {
      nodes {
        Image {
          localFile {
            childImageSharp {
              gatsbyImageData(layout: CONSTRAINED, formats: [AUTO, WEBP, AVIF])
            }
          }
        }
        Name
        Position
      }
    }
  }`);

  return data.allStrapiCommitteeMemberLists.nodes.map(result => {
    if (result.Name) {
      return (<div className="column is-half columns">
        <div className="column">
          <GatsbyImage image={result.Image.localFile.childImageSharp.gatsbyImageData} alt={result.Name} className="picture" />
        </div>
        <div className="column">
          <div className="innerName">{result.Name}</div>
          <div className="innerPosition">{result.Position}</div>
        </div>
      </div>)
    } else {
      return <div className="column is-half" />
    }
  })
};

export default CommitteeMemberList;